import React, { Component } from "react";
import update from "immutability-helper";
import classNames from "classnames";
import "./minesweeperhex.scss";

const Header = props => (
	<div id="header">
		<span id="time">{(Math.floor((Date.now() - (props.startTime || Date.now())) / 1000) + "").padStart(3, "0")}</span>
		<button onClick={props.reset}></button>
		<span id="mines-left">{(props.minesLeft < 0 ? "-" : "") + (Math.abs(props.minesLeft) + "").padStart(props.minesLeft < 0 ? 2 : 3, "0")}</span>
	</div>
);

const Tile = props => (
	<div className={classNames("tile", {
		"hidden": props.hidden,
		"flagged": props.flag === 1,
		"unsure": props.flag === 2,
		"zero": !props.hidden && props.value === 0,
		"one": !props.hidden && props.value === 1,
		"two": !props.hidden && props.value === 2,
		"three": !props.hidden && props.value === 3,
		"four": !props.hidden && props.value === 4,
		"five": !props.hidden && props.value === 5,
		"six": !props.hidden && props.value === 6,
		"seven": !props.hidden && props.value === 7,
		"eight": !props.hidden && props.value === 8,
		"bomb": (!props.hidden || props.gameEnded) && props.value === -1
	})} onClick={props.onClick} onContextMenu={props.onContextMenu}>
		{props.hidden || <span>{props.value}</span>}
	</div>
);

export default class Minesweeper extends Component {
	constructor(props){
		super(props);
		this.handleClick = this.handleClick.bind(this);
		this.handleRightClick = this.handleRightClick.bind(this);
		this.startNewGame = this.startNewGame.bind(this);
		this.state = {
			width: 25,
			height: 30,
			mines: 70,
			startTime: 0,
			gameEnded: false,
			hex: true
		};
		this.startNewGame(true);
	}
	getSurroundingCoordinates(x, y, width, height, hex){
		const coords = [];
		if(hex){
			const offset = (y & 1) === 0 ? -1 : 1;
			coords.push({x: x+1, y}, {x: x-1, y}, {x, y: y+1}, {x, y: y-1}, {x: x+offset, y: y+1}, {x: x+offset, y: y-1});
		}else{
			coords.push({x: x-1, y: y-1}, {x: x-1, y}, {x: x-1, y: y+1}, {x, y: y-1}, {x, y: y+1}, {x: x+1, y: y-1}, {x: y+1, y}, {x: x+1, y: y+1});
		}
		return coords.filter(coord => coord.x >= 0 && coord.y >= 0 && coord.x < width && coord.y < height);
	}
	generateField(width, height, mines, clickX, clickY){
		const randomNumber = max => Math.floor(Math.random() * max);
		let field = Array.from({length: height}, () => Array.from({length: width}, () => ({value: 0, hidden: true, flag: 0})));
		let minesLeft = mines;
		while(minesLeft > 0){
			const x = randomNumber(width);
			const y = randomNumber(height);
			if(field[y][x].value === -1) continue;
			if(x === clickX && y === clickY) continue;
			field[y][x].value = -1;
			minesLeft--;
		}
		for(let i = 0; i < width; i++){
			for(let j = 0; j < height; j++){
				if(field[j][i].value === -1){
					this.getSurroundingCoordinates(i, j, width, height, this.state.hex).forEach(({x, y}) => {
						if(field[y][x].value !== -1) field[y][x].value++;
					});
				}
			}
		}
		return field;
	}
	startNewGame(initial){
		this.stopGame();
		const {width, height} = this.state;
		document.documentElement.style.setProperty("--field-width", width);
		document.documentElement.style.setProperty("--field-height", height);
		let state = {
			started: false,
			gameEnded: false,
			field: Array.from({length: height}, () => Array.from({length: width}, () => ({value: 0, hidden: true, flag: 0}))),
			startTime: 0
		};
		if(initial) this.state = Object.assign(this.state, state);
		else this.setState(state);
	}
	getFieldsToReveal(x, y, toReveal, field){
		if((toReveal[y] || []).includes(x)) return toReveal;
		let tile = field[y][x];
		if(!tile.hidden || tile.flag === 1) return toReveal;
		if(Array.isArray(toReveal[y])) toReveal[y].push(x);
		else toReveal[y] = [x];
		if(tile.value === 0){
			this.getSurroundingCoordinates(x, y, this.state.width, this.state.height, this.state.hex).forEach(pos => {
				toReveal = this.getFieldsToReveal(pos.x, pos.y, toReveal, field);
			});
		}
		return toReveal;
	}
	handleClick(x, y){
		if(this.state.gameEnded) return;
		if(!this.state.started){
			const {width, height, mines} = this.state;
			const field = this.generateField(width, height, mines, x, y);
			let toReveal = this.getFieldsToReveal(x, y, [], field);
			let updater = {};
			Object.entries(toReveal).forEach(([y, xs]) => {
				updater[y] = {};
				xs.forEach(x => updater[y][x] = {hidden: {$set: false}});
			});
			this.state.field.forEach((column, y) => column.forEach((tile, x) => {
				if(tile.flag === 0) return;
				if(!Array.isArray(updater[y])) updater[y] = [];
				if(typeof updater[y][x] === "object") updater[y][x].flag = {$set: tile.flag};
				else updater[y][x] = {flag: {$set: tile.flag}};
			}));
			this.setState({
				started: true,
				field: update(field, updater),
				startTime: Date.now()
			});
			this.updateInterval = setInterval(()=>this.forceUpdate(), 1000);
		}else if(this.state.field[y][x].flag !== 1){
			const tile = this.state.field[y][x];
			if(tile.value === -1){
				this.setState({
					field: update(this.state.field, {[y]: {[x]: {hidden: {$set: false}}}}),
					gameEnded: true
				}, () => this.stopGame(false))
			}else{
				let toReveal = this.getFieldsToReveal(x, y, [], this.state.field);
				let updater = {};
				Object.entries(toReveal).forEach(([y, xs]) => {
					updater[y] = {};
					xs.forEach(x => updater[y][x] = {hidden: {$set: false}, flag: {$set: 0}});
				});
				this.setState({
					field: update(this.state.field, updater)
				}, () => {
					if(this.state.field.flatMap(column => column.filter(tile => tile.value !== -1 || !tile.hidden)).length === 0){
						this.setState({gameEnded: true});
						this.stopGame(true);
					}
				});
			}
		}
	}
	handleRightClick(e, x, y){
		e.preventDefault();
		if(this.state.gameEnded) return;
		if(this.state.field[y][x].hidden){
			this.setState({
				field: update(this.state.field, {[y]: {[x]: {flag: {$apply: val => (val+1)%3}}}})
			});
		}
	}
	stopGame(won){
		clearInterval(this.updateInterval);
		if(won === undefined) return;
		console.log(won)
	}
	render() {
		return (
			<div id="minesweeperhex" className={classNames({"hex": this.state.hex})}>
				<Header startTime={this.state.startTime} minesLeft={this.state.mines - this.state.field.flatMap(column => column.filter(tile => tile.flag === 1)).length} reset={()=>this.startNewGame()} />
				<div id="game">
					{this.state.field.map((column, i) => <div className="row">
						{column.map((tile, j) => <Tile value={tile.value} hidden={tile.hidden} flag={tile.flag} gameEnded={this.state.gameEnded} onClick={()=>this.handleClick(j, i)} onContextMenu={e=>this.handleRightClick(e, j, i)} />)}
					</div>)}
				</div>
			</div>
		)
	}
}
